@use '../config/_config' as *;
@use 'sass:map';

.section-barber {
    // &__container {
    //     @include spaceImage($maxWidth: 1920px, $imgHeight: 768, $imgWidth: 1183);
        
    //     &::after {
    //         @include bgTransparent($transparent: 0.7);
    //     }
    // }

    // &__bgImg {
    //     @include imgToFillSpace;
    //     object-fit: cover;
    // }

    &__container {
        position: relative;
        padding-top: 50px;

        &::after {
            @include bgTransparent($transparent: 0.7);
        }
    }

    &__bg-img {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__content {
        position: relative;
        z-index: 5;
    }

    // &__content {
    //     padding-bottom: 100px;
    //     text-align: left;
    // }

    &__img-container {
        margin: auto;
        @include spaceImage($maxWidth: 230px, $imgHeight: 480px, $imgWidth: 168px);
    }

    &__image {
        @include imgToFillSpace;
    }

    &__title {
        color: map.get($colorText, "regular");
        font-size: $fontTitle;
    }

    &__sub-title {
        color: map.get($colorText, "regular");
    }

    &__phone {
        color: map.get($colorPrimary, "regular");
        font-size: 25px;
    }

    &__owner {
        color: map.get($colorPrimary, "regular");
    }

    @media(max-width: 767px) {
        &__sosmed {
            margin-bottom: 30px;
            justify-content: center;
        }
    }

    // @media(max-width: 575px) {
    //     &__container {
    //         @include spaceImage($maxWidth: 1920px, $imgHeight: 618, $imgWidth: 558);        
    //     }

    //     &__content {
    //         padding-bottom: 30px;
    //         text-align: center;
    //         font-size: 12px;
    //     }        
    // }

    @media(max-width: 425px) {
        &__phone {
            font-size: 20px;
        }
    }
}