@use '../config/_config' as *;

@forward '../config/_config';

.section-brands {
    &__logos-container {
        @include spaceImage($maxWidth: 248px, $imgHeight: 139, $imgWidth: 500);
    }

    &__logo {
        @include imgToFillSpace;
    }

    @media(max-width: 575px) {
        &__logos-container {
            margin-bottom: 15px;
        }
    }
}