@use '../config/_config' as *;
@use "sass:map";

.swiper {
    width: 100%;
    // height: 85vh;
    // min-height: 350px;

    &--barber {
        @extend .swiper;
        height: 100%;
    }
}

.swiper-button-next {
    right: 20px !important;

    &::after {
        padding: 15px;
        border: 1px solid $colorLine;
        font-size: 10px !important;
        background-color: $colorBgCard;
        color: #ffffff;
    }

    @media(max-width: 425px) {
        right: 10px !important;
        
        &::after {
            padding: 10px
        }
    }

    &--barber {
        @extend .swiper-button-next;

        &::after {
            font-weight: map.get($textBold, "boldest");
            background-color: transparent;
            color: #000000;
        }
    }
}

.swiper-button-prev {
    left: 20px !important;

    &::after {
        padding: 15px;
        border: 1px solid $colorLine;
        font-size: 10px !important;
        background-color: $colorBgCard;
        color: #ffffff;
    }

    @media(max-width: 425px) {
        left: 10px !important;
        
        &::after {
            padding: 10px
        }
    }

    &--barber {
        @extend .swiper-button-prev;

        &::after {
            font-weight: map.get($textBold, "boldest");
            background-color: transparent;
            color: #000000;
        }
    }
}